import React from "react";

import LightboxBody from "../lightbox";

import './review.scss';

import rev1 from '../lightbox/rev1.jpg';
import rev2 from '../lightbox/rev2.jpg';
import rev3 from '../lightbox/rev3.jpg';
import rev4 from '../lightbox/rev4.jpg';
import rev5 from '../lightbox/rev5.jpg';

const Review = () => {
  return(
    <section className="review">
      <div className="container">
        <div className="review__content">
          <p className="review__pretitle"></p>
          <h2 className="review__title">       </h2>
          <p className="review__description">
                  100 , 800   500 .    , , Huawei, ,       .  ,     :
          </p>
        </div>
        <div className="review__pictures">
          <div className="picture">
            <figure>
              <img src={rev1} alt="" />
              <figcaption> ""</figcaption>
            </figure>
          </div>
          <div className="picture">
          <figure>
              <img src={rev5} alt="" />
              <figcaption> ""</figcaption>
            </figure>
          </div>
          <div className="picture">
          <figure>
              <img src={rev3} alt="" />
              <figcaption> </figcaption>
            </figure>
          </div>
          <div className="picture">
          <figure>
              <img src={rev4} alt="" />
              <figcaption>Life :)</figcaption>
            </figure>
          </div>
        </div>
        <div className="lightbox__container">
          <LightboxBody />
        </div>
      </div>
    </section>
  )
}

export default Review;